//for  run of WOW library
new WOW().init();


// for calculator in index.html numbers
try {
  const observer = new IntersectionObserver(
    entries => {
      entries.forEach(entry => {
        if (entry.intersectionRatio >= 0.1) {
          runAnimtion();
          observer.disconnect();
        }
      });
    },
    {
      threshold: 0.1
    }
  );
  observer.observe(document.querySelector('.advantages__list'));
  const bigNumbers = document.querySelectorAll('.advantages__numbers');

  function runAnimtion() {
    for (let number of bigNumbers) {
      anime.timeline().add({
        targets: number,
        innerHTML: ['', number.dataset.calc],
        easing: 'linear',
        autoplay: true,
        round: 1,
        duration: 2000
      }).add({
          targets: number.nextElementSibling,
          opacity: [0, 1],
          easing: 'linear'
        }
      );
    }
  }

} catch (err) {
  console.log(err);

}

// mobile menu
const btnMenu = document.querySelector('.header__menu-btn');
const menu = document.querySelector('.nav-list');
btnMenu.addEventListener('click', event => {
  menu.classList.toggle('nav-list--disable');
});

// Modal in products
const modal = document.querySelector('.modal-back');
const modalBtn = document.querySelector('.modal__close');
//for all of products in list
const productsPage = document.querySelector('.products-page');
// if products only one in page for example in product-item.html
const productsGetBtn = document.querySelector('.products__get');
// if have the list on page
if(modal){
  if (productsPage) {
    productsPage.addEventListener('click', event => {
      if (event.target.classList.contains('products__get')) {
        modal.classList.remove('modal__disable');
        anime({
          targets: modal,
          opacity: [0, 1],
          duration: 150,
          easing: 'linear'
        });
      }
    });
  } else if (productsGetBtn) {
    //if list does not have on page
    productsGetBtn.addEventListener('click', event => {
      modal.classList.remove('modal__disable');
      anime({
        targets: modal,
        opacity: [0, 1],
        duration: 150,
        easing: 'linear'
      });
    });
  }
  modalBtn.addEventListener('click', (event) => {
    modal.classList.add('modal__disable');
  });
  modal.addEventListener('click', event => {
    if (event.target === modal) {
      modal.classList.add('modal__disable');
    }
  });


}

const filterList = document.querySelector('.filter-list');
const filterItems = document.querySelectorAll('.filter-item')
const lists = document.querySelectorAll(`[data-filteredlist]`)
if (filterList) {

  filterList.addEventListener('click', event => {
    if (event.target.dataset.listtoggle) {
      const currentElement = event.target

      for (let filterItem of filterItems){
        filterItem.classList.remove('filter-item--active')
      }
      currentElement.parentElement.classList.add('filter-item--active')


      const listtoggle = event.target.dataset.listtoggle;

      const currentList = document.querySelector(`[data-list="${listtoggle}"]`);
      for (let list of lists){
        list.classList.add('list--hidden')
      }
      currentList.classList.remove('list--hidden')
    }
  });
}


